import React, {Component} from 'react';
import {Button} from 'antd';
import {reqGetQuestionsAnswer, reqAddTeachToMe} from '../../api';
import {resultType} from './Variable';

class Main extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectPaper: {},
            showData: {}
        };
    }

    handleClick = async (key, re) => {
        const {showData} = this.state;
        const payload = {
            questionsId: re.id || 1,
            ...re,
        };
        if (key === 'show') {
            const result = await reqGetQuestionsAnswer(payload);
            if (result.data) {
                const obj = {
                    [payload.questionsId]: result.data[0],
                };
                this.setState({showData: {...showData, ...obj}})
            }
            console.log('00999sf', result);
        }
        if (key === 'add') {
            const result = await reqAddTeachToMe(payload);
            console.log('00999sf', result);
        }
    };

    render() {
        const {result} = this.props;
        const {showData} = this.state;
        console.log('========sf',showData,)
        return (
            <div>
                {(result || []).map((re, index) => {
                    console.log('---sf',showData[re.questionsId || 1])
                    return (
                        <div key={index} className="content-main">
                            <span style={{padding: 10}}>{re.questionsStart}</span>
                            <div>
                                {Object.keys(showData).length > 0 && showData[re.questionsId || index+1] && Object.keys(resultType).map((t) => {
                                    return (<div key={t} style={{marginTop:10}}>
                                        <span>{`【${t}】`}</span>
                                        <p style={{paddingLeft:10}}>{showData[re.questionsId || index+1][resultType[t]] || ''}</p>
                                    </div>)
                                })}
                            </div>
                            <div className="foot">
                                <Button className="showButton" onClick={() => this.handleClick('show', re, index)}>
                                    查看答案
                                </Button>
                                <span className="addButton" onClick={() => this.handleClick('add', re, index)}>
                  +添加到我的
                </span>
                            </div>
                        </div>
                    );
                })}
            </div>
        );
    }
}

export default Main;
