import React, { Component } from 'react';
import { Select, Input, Button, message } from 'antd';
import { reqAddQuestions, reqGetPublic } from '../../api';
import { questionType, gradeType, courseType, complexityType, yearType,topDataType } from './Variable';
import { reqGetYears, reqGetLabel, reqGetGrade, reqGetCourse, reqGetKnowledges,reqGetAllQuestions} from '../../api';
const { TextArea } = Input;

const { Option } = Select;

class AddPaper extends Component {
  constructor(props) {
    super(props);
    this.state = {
      questionsStart: '',
      questionsAnalyze: '',
      questionsAnswer: '',
      questionsRemark: '',
      selectPaper:{},
      topData:{
        questionsType:['单选题','多选题' ,'判断题','填空题','解答题'],
        complexity:[ '容易','较易','一般','难','较难','困难']
      }
    };
    this.payload={
      complexity:'-1',
      course:null,
      grade:null,
      knowledge:null,
      label:null,
    }
  }

  async componentDidMount() {
    let results = await reqGetAllQuestions();
    if(results.data){
      this.setState({topData: {...results.data,
          questionsType:['单选题','多选题' ,'判断题','填空题','解答题'],
          complexity:['容易','较易','一般','难','较难','困难']}})
    }
  }

  handleChange = (key, value) => {
    const { selectPaper } = this.state;
    this.setState({ selectPaper: { ...selectPaper, [key]: value} });
  };
  handleChangeInput = (key,value) =>{
    this.setState({[key]:value})
  };

  handleAddPaper = async () => {
    const {selectPaper,questionsStart,questionsAnalyze,questionsAnswer,questionsRemark} = this.state;
    const payload ={
      ...this.payload,
      ...selectPaper,
      questionsStart,
      questionsAnalyze,
      questionsAnswer,
      questionsRemark,
    };
    if(!payload.grade){
      payload.grade= payload.grad || '初三';
    }
    let result = await reqAddQuestions(payload);
    if (result.status === 200) {
      message.success(result.message);
    } else {
      message.error(result.message);
    }
  };


  render() {
    const { topData,selectPaper } = this.state;
    return (
      <div className="paper">
        <div style={{ display: 'flex', flexWrap: 'wrap' }}>
          {Object.keys(topDataType).map((t,index)=>{
            return (<div className="paper-card" key={index}>
              <span className="paper-span">{t}</span>
              <Select
                value={selectPaper[topDataType[t]]}
                style={{ width: 120 }}
                onChange={(value) => this.handleChange(topDataType[t], value)}
              >
                {(topData[topDataType[t]]||[]).map((s, index) => {
                  let newValue = s;
                  if(t === '难易程度' || t === '题型'){
                    newValue = index+1
                  }
                  return (
                    <Option value={newValue} key={index + 1}>
                      {s}
                    </Option>
                  );
                })}
              </Select>
            </div>)
          })}
        </div>
        <div style={{ display: 'flex' }}>
          <div style={{ width: '50%' }}>
            <div className="paper-Input">
              <span className="paper-span">题干</span>
              <TextArea
                className="input"
                onChange={(e) => this.handleChangeInput('questionsStart', e.target.value)}
              />
            </div>
            <div className="paper-Input">
              <span className="paper-span">分析</span>
              <TextArea
                className="input"
                onChange={(e) => this.handleChangeInput('questionsAnalyze', e.target.value)}
              />
            </div>
          </div>

          <div style={{ width: '50%' }}>
            <div className="paper-Input">
              <span className="paper-span">答案</span>
              <TextArea
                className="input"
                onChange={(e) => this.handleChangeInput('questionsAnswer', e.target.value)}
              />
            </div>
            <div className="paper-Input">
              <span className="paper-span">点评</span>
              <TextArea
                className="input"
                onChange={(e) => this.handleChangeInput('questionsRemark', e.target.value)}
              />
            </div>
          </div>
        </div>
        <div style={{ display: 'flex', marginTop: 20 }}>
          <span className="foot-span">解答</span>
          <TextArea
            className="TextArea"
            onChange={(e) => this.handleChangeInput('questionsExplain', e.target.value)}
          />
        </div>
        <Button className="foot-btn" onClick={this.handleAddPaper}>
          添加
        </Button>
      </div>
    );
  }
}

export default AddPaper;