import React, { Component } from 'react';
import { Tree } from 'antd';

const { TreeNode } = Tree;

class TeacherTree extends Component {
  r_TreeItem = (data,parentName) => {
    return data.map((t) => {
      if (t.childNode && t.childNode.length > 0) {
        if(!t.parentName){
          t.parentName = parentName ? `${parentName}|${t.name}`:t.name
        }
        return (
          <TreeNode selectable={false} key={t.id} title={t.name}>
            {this.r_TreeItem(t.childNode, t.parentName)}
          </TreeNode>
        );
      }
      return <TreeNode selectable={true} key={`${t.id}|${parentName || ''}|${t.name}`} title={t.name}  />;
    });
  };
  handleSelect = (value) => {
    this.props.onChange(value);
  };
  render() {
    const { treeList } = this.props;

    return (
      <div style={{width:300}}>
        <Tree showLine onSelect={this.handleSelect}>
          {this.r_TreeItem(treeList)}
        </Tree>
      </div>


    );
  }
}

export default TeacherTree;
