import React, { Component } from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import memoryUtils from '../../utils/memoryUtils';
import Home from '../home';
import AddPaper from '../addPaper';
import ShowPaper from '../showPaper';
import ShowPlan from '../showPlan';
import AddPlan from '../addPlan';
// import Main from '../Main';
import '../style.less';

import { Layout } from 'antd';
import TeachHeader from '../../components/Header';
const { Footer, Content } = Layout;
export default class Admin extends Component {
  render() {
    const user = memoryUtils.user;
    console.log('===========sf', user);
    if (!user || !user.name) {
      // 当前没有登录
      return <Redirect to="/login" />;
    }
    return (
      <Layout style={{ height: '100%' }}>
        <TeachHeader />
        <Content style={{ backgroundColor: '#fff', margin: 20 }}>
          <Switch>
            <Route path="/home" component={Home} />
         {/*   <Route path="/my-main" component={Main} />*/}
            <Route path="/add-paper" component={AddPaper} />
            <Route path="/show-paper" component={ShowPaper} />
            <Route path="/show-plan" component={ShowPlan} />
            <Route path="/add-plan" component={AddPlan} />
            <Redirect to="/home" />
          </Switch>
        </Content>
        <Footer style={{ textAlign: 'center', color: '#ccc' }}>
          推荐使用Chrome浏览器,可以获得更好的体验
        </Footer>
      </Layout>
    );
  }
}
