import axios from 'axios';
import { message } from 'antd';
/*
	重新封装axios
	返回 Promise 对象

*/
const AXIOS_HEADER = {
  'Content-Type': 'application/json;',
};

const instance = axios.create({
  headers: AXIOS_HEADER,
});

export default function XHR(url, data = {}, method = 'GET') {
  return new Promise((resolve, reject) => {
    let promise = instance({ method, url, data });
    promise
      .then((response) => {
        resolve(response.data);
      })
      .catch((err) => {
        message.error('请求出错');
        resolve({
          err:'err'
        });
      });
  });
}
