import React  from 'react';
import { Modal } from 'antd';
import './style.less'

class AddPlanModal extends React.Component {
  state = { visible: false,value:[] };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = e => {
    const {onChange,Main} = this.props;
    const {value} = this.state;
    this.setState({
      visible: false,
    });
    if(value.length !== 0){
      onChange(Main,value)
    }

  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  render() {
    const {Main} = this.props;
    const arr =['seniorQuestion','pointsQuestion']
    return (
      <div className='my-plan'>
        {Main&&arr.indexOf(Main) >-1?<div className="add-btn" onClick={this.showModal}>
          +添加试题
        </div>:
        <div onClick={this.showModal} className="content-btn">+</div>}
        <Modal
          title="Basic Modal"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
        >
          <p>待做</p>
        </Modal>
      </div>
    );
  }
}
export default AddPlanModal