import axios from './axios';

const BASE_URL = '';

//登录
export const reqLogin = (params) => axios(BASE_URL + '/user/login', params, 'POST');

//忘记密码
export const reqUpdatePassword = (params) =>
  axios(BASE_URL + '/user/updatePassword', params, 'POST');

// 创建账号
export const reqAddUser = (params) => axios(BASE_URL + '/user/add-user', params, 'POST');

// 查看答案
export const reqGetQuestionsAnswer = (params) =>
  axios(`${BASE_URL}/questions/query-answer?questionsId=${params.questionsId}`, params, 'GET');

// 查看试题
export const reqGetQuestions = (params) =>
  axios(`${BASE_URL}/questions/query-questions`, params, 'POST');

// 添加到我
export const reqAddQuestionsToMe = (params) =>
  axios(`${BASE_URL}/questions/add-me?questionsId=${params.questionsId}`, params, 'GET');

// 新增试题
export const reqAddQuestions = (params) =>
  axios(`${BASE_URL}/questions/add-questions`, params, 'POST');

//删除教案
export const reqDeleteTeach = (params) =>
  axios(`${BASE_URL}/teach/delete?teachId=${params.teachId}`, params, 'GET');
//新增教案
export const reqAddTeach = (params) => axios(`${BASE_URL}/teach/add-teach`, params, 'POST');
// 查询教案
export const reqSearchTeach = (params) => axios(`${BASE_URL}/teach/query-teach`, params, 'POST');
//添加到我
export const reqAddTeachToMe = (params) =>
  axios(`${BASE_URL}/teach/add-me?questionsId=${params.questionsId}`, params, 'GET');
// 下载word 形式
export const reqGetTeachWord = (params) => axios(`${BASE_URL}/teach/download`, params, 'GET');
//   修改教案
export const reqUpdateTeach = (params) =>
  axios(`${BASE_URL}/personal/update-teach`, params, 'POST');

//教案左侧菜单
export const reqGetLeftMenu = (params) => axios(`${BASE_URL}/public/menuQueryTeach`, params, 'GET');
//查询所有的课题，备课时间
export const reqGetAllTeaches = (params) =>
  axios(`${BASE_URL}/public/synQueryTeach`, params, 'GET');
//试题左侧菜单
export const reqGetQuestionsLeftMenu = (params) =>
  axios(`${BASE_URL}/public/menuQueryQuestions`, params, 'GET');
//试题新增，试题综合查询接口，查询所有的年份，标签，知识点
export const reqGetAllQuestions = (params) =>
  axios(`${BASE_URL}/public/synQueryQuestions`, params, 'GET');

//查询年份
export const reqGetYears = (params) =>
  axios(`${BASE_URL}/public/year?dataType=${params.dataType}`, params, 'GET');
//查询所有标签
export const reqGetLabel = (params) =>
  axios(`${BASE_URL}/public/label?dataType=${params.dataType}`, params, 'GET');
//查询所有的年级
export const reqGetGrade = (params) =>
  axios(`${BASE_URL}/public/grade?dataType=${params.dataType}`, params, 'GET');
//查询所有的课程
export const reqGetCourse = (params) =>
  axios(`${BASE_URL}/public/course?dataType=${params.dataType}`, params, 'GET');
//查询所有知识点
export const reqGetKnowledges = (params) =>
  axios(`${BASE_URL}/public/knowledges?dataType=${params.dataType}`, params, 'GET');

//get public
export const reqGetPublic = (params) => axios(`${BASE_URL}/public/grade`, params, 'GET');
